@import "https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed&display=swap";
body {
  color: #fff;
  background: radial-gradient(#1f3756, #141539);
  margin: 0;
  padding: 0;
  font-family: Barlow Semi Condensed, sans-serif;
}

video {
  object-fit: cover;
  transform: scaleX(-1);
}

#app {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.schaar.active {
  background: #ec9e0e !important;
  border-bottom: 6px solid #bb6400 !important;
  box-shadow: 0 0 30px 2px #ffffff31, 0 0 70px 7px #ec9e0e4f !important;
}

.papier.active {
  background: #4865f4 !important;
  border-bottom: 6px solid #243dbc !important;
  box-shadow: 0 0 30px 2px #ffffff31, 0 0 70px 7px #0ed2ec4f !important;
}

.steen.active {
  background: #dc2e4e !important;
  border-bottom: 6px solid #962929 !important;
  box-shadow: 0 0 30px 2px #ffffff31, 0 0 70px 7px #dc2e4e7e !important;
}

#array {
  color: #fff;
  min-height: 20px;
  width: 720px;
  word-wrap: break-word;
  background-color: #242041;
  font-family: monospace;
}

#results {
  text-align: center;
  margin-top: 25px;
  font-size: 1.5em;
}

#scoreDiv {
  color: #2a46c0;
  letter-spacing: 2px;
  background: #fff;
  border-bottom: 6px solid #243dbc;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 10px;
  padding: 7px;
  font-size: 1.5em;
  font-weight: bold;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

#score {
  margin-left: 3px;
}

#test {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.item {
  box-shadow: none;
  background: gray;
  border-bottom: 6px solid #4d4d4d;
  border-radius: 25%;
  padding: 15px;
  transition: box-shadow .2s, background .2s, border-bottom .2s;
}

.item-img {
  height: 50px;
  width: 50px;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: flex;
  box-shadow: inset 0 5px 0 1px #c7c7c7b5;
}

#grid {
  grid-column-gap: 75px;
  grid-row-gap: 15px;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.picks {
  letter-spacing: 5px;
  text-align: center;
  margin-top: 5px;
  margin-bottom: 1rem;
  font-size: 1.5em;
}

.landmarks {
  flex-direction: row;
  column-gap: 20px;
  display: flex;
}

.container2 {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

